import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);


const store = new Vuex.Store({
  // !!!
  state: {
    showCss: true, // truefalse

    display: 'Home', // 
    lab_select: 0, // header 0
    lab_num: 0, // header

    content_id: 0, // ID

    _back:{  // 
      back_display:'home',   // display
      back_select: 0,   // 
      back_num: 0,  // 
    },

    lab:{  // leftBar
      lab_1:['','','','',''],
      lab_2:['',''],
      lab_3:['','','',''],
      lab_4:['','','','',''],
      lab_5:['','','','',''],
      lab_6:['',''],
      lab_7:['']
    },
  },

});

export default store







