<script>
  import Swiper from "swiper"
  import Header from '../pc/common/header/Header';
  export default {
    name: 'mobile',
    components: { Header },
    data(){
      return{
        conObj:{
          height:''
        }
      }
    },
    methods:{
      getHeight(){
        this.conObj.height = window.innerHeight + 'px';
      }
    },

    mounted() {
      var mySwiper = new Swiper('.swiper-container', {
        direction: 'vertical',
        mousewheel: true,
        loop: false,
        autoplay: true,
        effect: 'fade',
        pagination: {
          el: '.swiper-pagination',
        },
      })

    },
    created(){
      // 
      if ((navigator.userAgent.match(/(phone|pod|iPhone|ios|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)) && !navigator.userAgent.match(/(iPad)/i)) {
        console.log('');
      }else{
        console.log('PC');
        this.$router.push('/')
      }
      window.addEventListener('resize', this.getHeight);
      this.getHeight()
    },
    destroyed(){
      window.removeEventListener('resize', this.getHeight)
    }
  };
