<template>
  <div class="limited">

    <!--Header -->
    <HeaderBar class="top" />

    <!--Swiper-->
    <Home_page v-if="Layer === 'Home'"/>

    <!---->
    <Twice v-if="Layer === 'Twice'"/>

    <!---->
    <Third v-if="Layer === 'Third'"/>





    <!---->
    <Car_match v-if="Layer === 'Car_match'"/>

    <!---->
    <Job_All v-if="Layer === 'Job_All'"/>

    <!--footer-->
    <Footer class="top" v-if="Layer === 'Home'"/>

  </div>
</template>






<script>

  export default {

    name: 'Home',
    computed: {
      Layer () {
        return this.$store.state.display
      }
    },
    created(){
      // 
      if ((navigator.userAgent.match(/(phone|pod|iPhone|ios|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)) && !navigator.userAgent.match(/(iPad)/i)) {
        console.log('');
        this.$router.push('/mobile')
      }else{
        console.log('PC');
      }
    },



  };

</script>










<style scoped>
  .top{
    z-index: 99;
  }
  .limited{
    min-width: 1180px;
  }
</style>



