<template>


  <div class="swiper-container" :style="conObj">
    <div class="swiper-wrapper">

      <div class="swiper-slide" data-swiper-autoplay="5000" v-cloak>
        <div class="div a">
          <div ref="normal" class="w1" v-show="showX">
            <div class="w1_top">
              <a style="margin: auto"> () </a>
            </div>
            <div class="w1_mid">
              <a style="margin: auto">20062018 ()</a>
            </div>
            <div class="w1_button" @click="turn(from = 10,index = 0,to = 'Job_All')" >
              <a style="margin: auto"></a>
            </div>
          </div>
          <div ref="show" class="show" v-show="!showX">
            <div ref="show_con" class="show_con">
              <img src="../../../../../static/logo.png" alt="logo" class="show_con_img"/>
            </div>
          </div>
        </div>
      </div>

      <div class="swiper-slide">
        <div class="div b">
          <div class="w1">
            <div class="w1_top w1_top_b">
              <a style="margin: auto"></a>
            </div>
            <div class="w1_mid">
              <a style="margin: auto">11028</a>
            </div>
            <div class="w1_button" @click="OutSide">
              <a style="margin: auto"></a>
            </div>
          </div>
        </div>
      </div>

      <div class="swiper-slide">
        <div class="div c">
          <div class="w1">
            <div class="w1_top w1_top_b">
              <a style="margin: auto"></a>
            </div>
            <div class="w1_mid">
              <a style="margin: auto">2012  ;&nbsp;&nbsp;&nbsp;2015 CCPC;&nbsp;&nbsp;&nbsp;2018 CEVC </a>
            </div>
            <div class="w1_button" @click="turn(from = 10,index = 2,to = 'Car_match')">
              <a style="margin: auto" ></a>
            </div>
          </div>
        </div>
      </div>
    </div>


  </div>

</template>



<script>
  import Swiper from "swiper"

  export default {
    name: 'Home_page',
    inject:['turnTo'],
    data(){
      return{
        conObj:{
          height:''
        },
        showX: false
      }
    },
    methods:{
      async show_x(){ // 
        this.$store.state.showCss = false;
        let that = this;
        this.$refs.show_con.style.transition = 'height 500ms,width 600ms,border-color 600ms,opacity 300ms';
        this.$refs.show_con.style.height = '162px';
        setTimeout(function(){
          that.$refs.show_con.style.width = '312px';
          that.$refs.show_con.style.borderColor = 'rgba(255,255,255,0)';
        }, 500);
        setTimeout(function(){
          that.$refs.show_con.style.opacity = '0';
        }, 1500);
        setTimeout(function(){
          that.showX = !that.showX
        }, 1800);
      },
      getHeight(){
        this.conObj.height = window.innerHeight + 'px';
      },
      OutSide(){
        window.location.href="https://baike.baidu.com/item/%E5%A4%A9%E6%B4%A5%E4%B9%8B%E7%9C%BC/10021404?fr=aladdin";
      },
      turn (from,index,to){  // header
        this.turnTo(from,index,to);
      }
    },
    mounted() {
      var mySwiper = new Swiper('.swiper-container', {
        direction: 'vertical',
        mousewheel: true,
        loop: false,
        autoplay: {
          delay: 3500,
        },
        pagination: {
          el: '.swiper-pagination',
        },
      })
    },
    created(){
      window.addEventListener('resize', this.getHeight);
      this.getHeight();
      let that = this;
      if(this.$store.state.showCss){
        setTimeout(function(){
          that.show_x()
        }, 700);
      } else {
        this.showX = true
      }
    },
    destroyed(){
      window.removeEventListener('resize', this.getHeight)
    }

  };
</script>




<style scoped>

  .swiper-container{
    width: 100%;
  }
  .div{
    height: 100%;
    width: 100%;
    padding-top: 15%;
  }




  .show{
    display: flex;
    height: 160px;
    width: 100%;
    /*background-color: #c319ff;*/
  }
  .show_con{
    display: flex;
    height: 0;
    width: 0;
    margin: auto;
    border-right: 2px solid white;
    border-left: 2px solid white;
    border-radius: 2px;
    overflow: hidden;
  }
  .show_con_img{
    height: 160px;
    width: 310px;
    margin: auto;
  }




  .a{
    background: url("../../../pc/img/a.jpg") no-repeat center fixed;
    background-size:cover;
  }
  .b{
    background: url("../../../pc/img/b.jpg") no-repeat center;
    background-size:cover;
  }
  .c{
    background: url("../../../pc/img/c.jpg") no-repeat center;
    background-size:cover;
  }
  .w1{
    height: 300px;
    width: 800px;
    margin: 0 16%;
  }
  .w1_top{
    display: flex;
    height: 60px;
    width: 545px;
    color: white;
    font-size: 36px;
    font-weight: bolder;
    background: rgba(0,0,0,0.4);
  }
  .w1_top_b{
    width: 180px;
  }
  .w1_mid{
    display: flex;
    height: 100px;
    width: 100%;
    margin-top: 40px;
    padding: 0 26px;
    font-size: 14px;
    font-weight: bolder;
    color: white;
    background: rgba(0,0,0,0.4);
  }
  .w1_button{
    display: flex;
    height: 30px;
    width: 90px;
    margin-top: 40px;
    color: white;
    border: 1px solid rgba(251, 87, 31, 0);
    font-weight: bolder;
    box-shadow: 0 0 10px rgba(251, 87, 31, 0.8);
    background-color: rgba(251, 87, 31, 0.8);
  }
  .w1_button:hover{
    cursor: pointer;
    box-shadow: 0 0 10px rgba(251, 87, 31, 1);
    background-color: rgba(251, 87, 31, 1);
  }
</style>



